.cinema-ul{
    width:100%;
    padding:0 0 0 0.3rem;
    box-sizing:border-box;
    background:#fff;
    margin-bottom:1.5rem;
    .cinema-li{
        width:7.2rem;
        padding-top:0.4rem;
        box-sizing:border-box;
        border-bottom:0.01rem solid #e1e1e1;
        ol{
            width:100%;
            .nm-li{
                margin-bottom:0.22rem;
                .nm-span{
                    font-size:0.3rem;
                    font-weight:bold;
                    margin-right:0.2rem;
                }
                .price-span{
                    font-size:0.3rem;
                    color:#dd4d41;
                    font-weight:bold;
                }
            }
            .addr-li{
                width:6.22rem;
                line-height:0.3rem;
                font-size:0.24rem;
                color:#686868;
                margin-bottom:0.16rem;
            }
            .tag-li{
                width:7.2rem;
                flex-wrap:wrap;
                display:flex;
                margin-bottom:0.16rem;
                p{
                    height:0.3rem;
                    font-size:0.18rem;
                    text-align:center;
                    margin-right:0.1rem;
                    padding:0 0.07rem;
                    line-height:0.3rem;
                    margin-bottom:0.1rem;
                }
                .refund,.endorse,.hall{
                    color:#73bdf2;
                    border:0.02rem solid #73bdf2;
                }
                .snack,.vipCard{
                    color:#f09e38;
                    border:0.02rem solid #f09e38;
                }
            }
            .card-li{
                width:7.2rem;
                display:flex;
                margin-bottom:0.38rem;
                .card{
                    width:0.3rem;
                    height:0.3rem;
                    border-radius:0.03rem;
                    font-size:0.16rem;
                    line-height:0.3rem;
                    text-align:center;
                    background:#73bdf2;
                    color:#fff;
                    margin-right:0.1rem;
                }
                .tip{
                    color:#686868;
                    font-size:0.2rem;
                }
            }
        }
    }
}